<template v-if="current === 0" :key="0">
  <div>
    <Alert :type="status" show-icon v-if="hasResp" style="width: 65%; margin-left: 250px">
      {{title}}
      <span slot="desc" v-html="message"></span>
    </Alert>
    <Form ref="dal" :model="dal" :rules="ruleDal" :label-width="250" style="margin-top: 50px">
      <FormItem label="" prop="oldClusterName" style="width: 600px">
        <Input v-model="dal.oldClusterName" @input="changeOld" placeholder=""/>
      </FormItem>
      <FormItem label="" prop="newClusterName" style="width: 600px">
        <Input v-model="dal.newClusterName" placeholder=""/>
      </FormItem>
      <FormItem label="" prop="drcZone">
        <Select v-model="dal.drcZone" style="width: 200px"  placeholder="" @input="getAllDbs">
          <Option v-for="item in dal.drcZoneList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </FormItem>
      <FormItem label="" prop="env">
        <Select v-model="dal.env" style="width: 200px"  placeholder="" @input="changeEnv">
          <Option v-for="item in dal.envList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </FormItem>
      <FormItem label="DAL" style="width:90%">
        <div>
          <Table highlight-row border ref="selection" :columns="dal.columns" :data="dal.dbNames" v-model="dal.selectedDbs" @on-selection-change="changeSelection">
            <template slot-scope="{index}" slot="status">
              <Tag size="large" :color="dal.dbNames[index].status ? 'success':'default'">{{dal.dbNames[index].status ? '':''}}</Tag>
            </template>
            <template slot-scope="{index}" slot="is">
              <Tag size="large" :color="dal.dbNames[index].isShard ? 'warning':'primary'">{{dal.dbNames[index].isShard ? '':''}}</Tag>
            </template>
            <template slot-scope="{index}" slot="no">
              <Tag size="large" :color="'default'">{{dal.dbNames[index].shardStartNo}}</Tag>
            </template>
          </Table>
        </div>
      </FormItem>
      <FormItem>
        <Button @click="handleReset('dal')"></Button>
        <Button type="primary" style="margin-left: 150px" @click="changeModal('dal', 1)">DAL Cluster</Button>
        <Modal
          v-model="dal.modal1"
          title="DAL"
          @on-ok="postDal('dal', 'instance')">
          <p>dbdal cluster</p>
        </Modal>
<!--        <Button type="primary" @click="changeModal('dal', 2)" style="margin-left: 50px"></Button>-->
<!--        <Modal-->
<!--          v-model="dal.modal2"-->
<!--          title=""-->
<!--          @on-ok="postDal('dal', 'user')">-->
<!--          <p>dbdal cluster</p>-->
<!--        </Modal>-->
<!--        <Button type="success" @click="changeModal('dal', 3)" style="margin-left: 50px"></Button>-->
<!--        <Modal-->
<!--          v-model="dal.modal3"-->
<!--          title="dal cluster"-->
<!--          @on-ok="release('dal')">-->
<!--          <p>dal cluster</p>-->
<!--        </Modal>-->
      </FormItem>
    </Form>
  </div>
</template>
<script>
export default {
  name: 'dal',
  props: {
    oldClusterName: String,
    newClusterName: String,
    env: String,
    newDrcZone: String
  },
  data () {
    return {
      status: '',
      title: '',
      message: '',
      hasResp: false,
      dal: {
        modal1: false,
        modal2: false,
        modal3: false,
        oldClusterName: this.oldClusterName,
        newClusterName: this.newClusterName,
        env: this.env,
        drcZone: this.newDrcZone,
        columns: [
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title: 'DB',
            minWidth: 150,
            key: 'name'
          },
          {
            title: 'Dal Cluster',
            minWidth: 150,
            key: 'dalName'
          },
          {
            title: '',
            width: 110,
            slot: 'status'
          },
          {
            title: '',
            width: 100,
            slot: 'is'
          },
          {
            title: '',
            width: 100,
            slot: 'no'
          }
        ],
        dbNames: [],
        envList: [
          {
            value: 'product',
            label: 'PRODUCT'
          },
          {
            value: 'fat',
            label: 'FAT'
          },
          {
            value: 'lpt',
            label: 'LPT'
          },
          {
            value: 'uat',
            label: 'UAT'
          }
        ],
        drcZoneList: [
          {
            value: 'shaoy',
            label: ''
          },
          {
            value: 'shaxy',
            label: ''
          },
          {
            value: 'sharb',
            label: ''
          },
          {
            value: 'shajq',
            label: ''
          },
          {
            value: 'shafq',
            label: ''
          },
          {
            value: 'shajz',
            label: ''
          },
          {
            value: 'ntgxh',
            label: ''
          },
          {
            value: 'fraaws',
            label: 'AWS'
          },
          {
            value: 'shali',
            label: ''
          },
          {
            value: 'sinibuaws',
            label: 'IBU-VPC'
          },
          {
            value: 'sinaws',
            label: 'AWS'
          }
        ]
      },
      ruleDal: {
        dalClusterName: [
          { required: true, message: 'DAL', trigger: 'blur' }
        ],
        oldClusterName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        newClusterName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        env: [
          { required: true, message: '', trigger: 'blur' }
        ],
        drcZone: [
          { required: true, message: '', trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    postDal (name, goal) {
      const that = this
      that.$refs[name].validate((valid) => {
        if (!valid) {
          that.$Message.error('!')
        } else {
          // loading start
          this.start()
          const dbs = this.dal.selectedDbs
          that.hasResp = false
          that.message = ''
          that.title = '!'
          that.status = 'success'
          for (let i = 0; i < dbs.length; i++) {
            // // old cluster
            // that.axios.post('/api/drc/v1/access/clusters/env/' + that.dal.env + '/goal/' + goal, {
            //   dalclustername: dbs[i].dalName,
            //   dbname: dbs[i].name,
            //   clustername: that.dal.oldClusterName,
            //   isshard: dbs[i].isShard,
            //   shardstartno: dbs[i].isShard ? parseInt(dbs[i].shardStartNo) : 0
            // }).then(response => {
            //   that.hasResp = true
            //   if (response.data.data.success) {
            //     if (goal === 'instance') {
            //       that.message += 'DAL<br/>' + that.dal.oldClusterName + '' + dbs[i].dalName + '<br/>' + response.data.data.message + '<hr/>'
            //     } else if (goal === 'user') {
            //       that.message += '<br/>' + that.dal.oldClusterName + '' + dbs[i].dalName + '<br/>' + response.data.data.message + '<hr/>'
            //     }
            //   } else {
            //     that.title = '!'
            //     that.status = 'error'
            //     if (goal === 'instance') {
            //       that.message += 'DAL<br/>' + that.dal.oldClusterName + '' + dbs[i].dalName + '<br/>' + response.data.data.message + '<hr/>'
            //     } else if (goal === 'user') {
            //       that.message += '<br/>' + that.dal.oldClusterName + '' + dbs[i].dalName + '<br/>' + response.data.data.message + '<hr/>'
            //     }
            //   }
            //   // loading finish
            //   this.finish()
            // })
            // new cluster
            that.axios.post('/api/drc/v1/access/clusters/env/' + that.dal.env + '/goal/' + goal, {
              dalclustername: dbs[i].dalName,
              dbname: dbs[i].name,
              basedbname: dbs[i].basedbname,
              clustername: that.dal.newClusterName,
              isshard: dbs[i].isShard,
              shardstartno: dbs[i].isShard ? parseInt(dbs[i].shardStartNo) : 0
            }).then(response => {
              that.hasResp = true
              if (response.data.data.success) {
                if (goal === 'instance') {
                  that.message += 'DAL<br/>' + that.dal.newClusterName + ':' + dbs[i].dalName + '<br/>' + response.data.data.message + '<hr/>'
                } else if (goal === 'user') {
                  that.message += '<br/>' + that.dal.newClusterName + ':' + dbs[i].dalName + '<br/>' + response.data.data.message + '<hr/>'
                }
              } else {
                that.title = '!'
                that.status = 'error'
                if (goal === 'instance') {
                  that.message += 'DAL<br/>' + that.dal.newClusterName + ':' + dbs[i].dalName + '<br/>' + response.data.data.message + '<hr/>'
                } else if (goal === 'user') {
                  that.message += '<br/>' + that.dal.newClusterName + ':' + dbs[i].dalName + '<br/>' + response.data.data.message + '<hr/>'
                }
              }
              // loading finish
              this.finish()
            })
          }
        }
      })
    },
    release (name) {
      const that = this
      that.$refs[name].validate((valid) => {
        if (!valid) {
          that.$Message.error('!')
        } else {
          // loading start
          this.start()
          const dbs = this.dal.selectedDbs
          that.hasResp = false
          that.status = 'success'
          that.title = 'DAL!'
          that.message = ''
          for (let i = 0; i < dbs.length; i++) {
            that.axios.post('/api/drc/v1/access/clusters/clustername/' + dbs[i].dalName + '/env/' + that.dal.env + '/releases')
              .then(response => {
                that.hasResp = true
                if (response.data.data.status === 200) {
                  that.message += '<br/>' + dbs[i].dalName + '<br/>' + response.data.data.message + '<hr/>'
                } else {
                  that.status = 'error'
                  that.title = 'DAL!'
                  that.message += '<br/>' + dbs[i].dalName + '<br/>' + response.data.data.message + '<hr/>'
                }
                // loading finish
                this.finish()
              })
          }
        }
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
    changeEnv () {
      this.$emit('envChanged', this.dal.env)
      this.getAllDbs()
    },
    getAllDbs () {
      this.axios.get('/api/drc/v1/mha/dbnames/dalnames/cluster/' + this.dal.oldClusterName + '/env/' + this.dal.env + '/zoneId/' + this.dal.drcZone)
        .then(response => {
          console.log(response.data)
          this.dal.dbNames = []
          response.data.data.forEach(db => this.dal.dbNames.push({
            name: db.dbname,
            basedbname: db.basedbname,
            dalName: db.dalname,
            isShard: db.isshard,
            shardStartNo: db.shardstartno,
            status: db.status
          }))
        })
    },
    changeSelection () {
      this.dal.selectedDbs = this.$refs.selection.getSelection()
    },
    changeOld () {
      this.getAllDbs()
      this.$emit('oldClusterChanged', this.dal.oldClusterName)
    },
    changeIsShard (index) {
      if (!this.dal.dbNames[index].isshard) {
        this.dal.dbNames[index].shardStartNo = 0
      }
    },
    start () {
      this.$Loading.start()
    },
    finish () {
      this.$Loading.finish()
    },
    error () {
      this.$Loading.error()
    },
    changeModal (name, i) {
      this.$refs[name].validate((valid) => {
        if (!valid) {
          this.$Message.error('!')
        } else {
          switch (i) {
            case (1): this.dal.modal1 = true; break
            case (2): this.dal.modal2 = true; break
            case (3): this.dal.modal3 = true; break
          }
        }
      })
    }
  },
  created () {
    this.getAllDbs()
  }
}
</script>
<style scoped>
</style>
