<template v-if="current === 0" :key="0">
  <div>
    <Alert :type="status" show-icon v-if="hasResp" style="width: 65%; margin-left: 250px">
      {{title}}
      <span slot="desc" v-html="message"></span>
    </Alert>
    <Form ref="copy" :model="copy" :rules="ruleCopy" :label-width="250" style="margin-top: 50px">
      <FormItem label="" prop="oldClusterName" style="width: 600px">
        <Input v-model="copy.oldClusterName" @input="changeOld" placeholder=""/>
      </FormItem>
      <FormItem label="" prop="newClusterName" style="width: 600px">
        <Input v-model="copy.newClusterName" placeholder=""/>
      </FormItem>
      <FormItem>
        <Button @click="handleReset('copy')"></Button><br><br>
        <Button type="primary" @click="checkCopy('copy')"></Button>
        <Button type="error" :disabled="disabledDisconnect" @click="changeModal('copy')" style="margin-left: 50px"></Button>
        <Modal
          v-model="copy.modal"
          title=""
          @on-ok="disconnect('copy')">
          <p></p>
        </Modal>
      </FormItem>
    </Form>
  </div>
</template>
<script>
export default {
  name: 'copy',
  props: {
    oldClusterName: String,
    newClusterName: String
  },
  data () {
    return {
      status: '',
      title: '',
      message: '',
      hasResp: false,
      disabledDisconnect: true,
      copy: {
        modal: false,
        oldClusterName: this.oldClusterName,
        newClusterName: this.newClusterName
      },
      ruleCopy: {
        oldClusterName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        newClusterName: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    checkCopy (name) {
      const that = this
      that.$refs[name].validate((valid) => {
        if (!valid) {
          that.$Message.error('!')
        } else {
          // loading start
          this.start()
          that.hasResp = false
          that.axios.post('/api/drc/v1/access/copystatus', {
            OriginalCluster: this.copy.oldClusterName,
            DrcCluster: this.copy.newClusterName,
            rplswitch: 0
          }).then(response => {
            that.hasResp = true
            if (response.data.data.status === 'T') {
              that.disabledDisconnect = false
              that.status = 'success'
              that.title = '!'
            } else {
              that.disabledDisconnect = true
              that.status = 'error'
              that.title = '!'
            }
            that.message = response.data.data.message
            // loading finish
            this.finish()
          })
        }
      })
    },
    disconnect (name) {
      const that = this
      that.$refs[name].validate((valid) => {
        if (!valid) {
          that.$Message.error('!')
        } else {
          // loading start
          this.start()
          that.hasResp = false
          that.axios.post('/api/drc/v1/access/copystatus', {
            OriginalCluster: this.copy.oldClusterName,
            DrcCluster: this.copy.newClusterName,
            rplswitch: 1
          }).then(response => {
            that.hasResp = true
            if (response.data.data.status === 'T') {
              that.status = 'success'
              that.title = '!'
            } else {
              that.status = 'error'
              that.title = '!'
            }
            that.message = response.data.data.message
            // loading finish
            this.finish()
          })
        }
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
    changeOld () {
      this.$emit('oldClusterChanged', this.copy.oldClusterName)
    },
    start () {
      this.$Loading.start()
    },
    finish () {
      this.$Loading.finish()
    },
    error () {
      this.$Loading.error()
    },
    changeModal (name) {
      this.$refs[name].validate((valid) => {
        if (!valid) {
          this.$Message.error('!')
        } else {
          this.copy.modal = true
        }
      })
    }
  }
}
</script>
