<template v-if="current === 0" :key="0">
  <div>
    <Alert :type="status" show-icon v-if="hasResp" style="width: 65%; margin-left: 250px">
      {{title}}
      <span slot="desc" v-html="message"></span>
    </Alert>
    <Form ref="build" :model="build" :rules="ruleBuild" :label-width="250" style="margin-top: 50px">
      <FormItem label="" prop="oldClusterName" style="width: 600px">
        <Input v-model="build.oldClusterName" @input="changeOld" placeholder="" />
      </FormItem>
      <FormItem label="" prop="drcZone">
        <Select v-model="build.drcZone" style="width: 200px"  placeholder="" @input="changeNewZone">
          <Option v-for="item in build.drcZoneList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </FormItem>
      <FormItem label="BU" style="width: 600px" prop="bu">
        <Input v-model="build.bu" @input="changeBu" placeholder="BU" />
      </FormItem>
      <FormItem label="DAL Cluster" style="width: 600px" prop="dalclustername">
        <Input v-model="build.dalclustername" @input="changeDalClusterName" placeholder="DAL Cluster" />
      </FormItem>
      <FormItem label="appid" style="width: 600px" prop="appid">
        <Input v-model="build.appid" @input="changeAppId" placeholder="appid" />
      </FormItem>
      <FormItem>
        <Button @click="handleReset('build')"></Button>
        <Button type="primary" @click="changeModal('build')" style="margin-left: 150px"></Button>
        <Modal
          v-model="build.modal"
          title=""
          @on-ok="postBuild('build')">
          <p> "{{build.oldClusterName + build.drcZone}}" BU/DAL Cluster/appid "{{build.bu}}"/"{{build.dalclustername}}"/"{{build.appid}}"</p>
        </Modal>
      </FormItem>
    </Form>
  </div>
</template>
<script>
export default {
  name: 'build',
  props: {
    oldClusterName: String,
    newClusterName: String
  },
  data () {
    return {
      status: '',
      title: '',
      message: '',
      hasResp: false,
      build: {
        modal: false,
        oldClusterName: this.oldClusterName,
        newClusterName: this.newClusterName,
        bu: '',
        dalclustername: '',
        appid: '',
        drcZone: '',
        drcZoneList: [
          {
            value: 'shaoy',
            label: ''
          },
          {
            value: 'shaxy',
            label: ''
          },
          {
            value: 'sharb',
            label: ''
          },
          {
            value: 'shajq',
            label: ''
          },
          {
            value: 'shafq',
            label: ''
          },
          {
            value: 'shajz',
            label: ''
          },
          {
            value: 'ntgxh',
            label: ''
          },
          {
            value: 'fraaws',
            label: 'AWS'
          },
          {
            value: 'shali',
            label: ''
          },
          {
            value: 'sinibuaws',
            label: 'IBU-VPC'
          },
          {
            value: 'sinaws',
            label: 'AWS'
          }
        ]
      },
      ruleBuild: {
        oldClusterName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        drcZone: [
          { required: true, message: '', trigger: 'change' }
        ],
        bu: [
          { required: true, message: 'BU', trigger: 'blur' }
        ],
        dalclustername: [
          { required: true, message: 'DAL Cluster', trigger: 'blur' }
        ],
        appid: [
          { required: true, message: 'cluster appid', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    postBuild (name) {
      const that = this
      that.$refs[name].validate((valid) => {
        if (!valid) {
          that.$Message.error('!')
        } else {
          // loading start
          this.start()
          this.hasResp = false
          that.axios.post('/api/drc/v1/access/mhaV2', {
            clustername: this.build.oldClusterName,
            drczone: this.build.drcZone,
            bu: this.build.bu,
            dalclustername: this.build.dalclustername,
            appid: this.build.appid
          }).then(response => {
            that.hasResp = true
            if (response.data.data.status === 'T') {
              that.status = 'success'
              that.title = '!'
              that.message = '' + response.data.data.drcCluster
              that.$emit('newClusterChanged', response.data.data.drcCluster)
            } else if (response.data.data.status === 'W') {
              that.status = 'establishing'
              this.title = '...'
            } else {
              that.status = 'error'
              that.title = '!'
            }
            // loading finish
            this.finish()
          })
        }
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
    changeOld () {
      this.$emit('oldClusterChanged', this.build.oldClusterName)
    },
    changeNewZone () {
      this.$emit('newDrcZoneChanged', this.build.drcZone)
    },
    changeBu () {
      this.$emit('buChanged', this.build.bu)
    },
    changeDalClusterName () {
      this.$emit('dalclusternameChanged', this.build.dalclustername)
    },
    changeAppId () {
      this.$emit('appidChanged', this.build.appid)
    },
    start () {
      this.$Loading.start()
    },
    finish () {
      this.$Loading.finish()
    },
    error () {
      this.$Loading.error()
    },
    changeModal (name) {
      this.$refs[name].validate((valid) => {
        if (!valid) {
          this.$Message.error('!')
        } else {
          this.build.modal = true
        }
      })
    }
  }
}
</script>
<style scoped>
</style>
